import React from "react";
import { Container } from "../../globalStyles";
import gestionImg from "../../images/gestionImg.png";
import bitnetImg from "../../images/bitnetLogo.svg";

import {
  TextWrapper,
  ProjectHeading,
  ProjectSection,
  ProjectRow,
  ProjectColumn,
  ProjectCard,
  CardTopLine,
  CardHeader,
  CardImgWrapper,
  CardImg,
} from "./Projects.elements";

const Projects = () => {
  return (
    <>
      <Container>
        <ProjectSection>
          <TextWrapper>
            <ProjectHeading className="color-dark">Proyectos</ProjectHeading>
          </TextWrapper>
          <ProjectRow>
            <ProjectColumn>
              <ProjectCard className="bg-slightpurple">
                <CardTopLine>Laravel</CardTopLine>
                <CardHeader>Gestion Municipal de Pedidos</CardHeader>
                <CardImgWrapper>
                  <CardImg src={gestionImg}></CardImg>
                </CardImgWrapper>
              </ProjectCard>
            </ProjectColumn>
            <ProjectColumn>
              <ProjectCard className="bg-salmon space-left space-bottom">
                <CardTopLine className="color-dark">Laravel</CardTopLine>
                <CardHeader className="color-dark">BitNet</CardHeader>
                <CardImgWrapper>
                  <CardImg className="bitnetLogo" src={bitnetImg}></CardImg>
                </CardImgWrapper>
              </ProjectCard>
              <ProjectCard className="bg-lightgreen space-left">
                <CardTopLine>React</CardTopLine>
                <CardHeader>Portfolio</CardHeader>
                <CardImgWrapper>
                  <CardImg src={gestionImg}></CardImg>
                </CardImgWrapper>
              </ProjectCard>
            </ProjectColumn>
          </ProjectRow>
        </ProjectSection>
      </Container>
    </>
  );
};

export default Projects;
