import React from "react";
import { Link } from "react-router-dom";
import { Container } from "../../globalStyles";

// import linkedinIcon from "../../images/linkedin.svg";
// import githubIcon from "../../images/github.svg";
// import AboutImg from "../../images/heroImg.svg";

import {
  AboutSection,
  AboutRow,
  AboutColumn,
  TextWrapper,
  TopLine,
  SubTitle,
} from "./About.elements";

function About() {
  return (
    <>
      <AboutSection>
        <Container>
          <AboutRow>
            <AboutColumn>
              <TextWrapper>
                <TopLine>Sobre Mi</TopLine>
                <SubTitle>
                  Soy Matias Nuñez Crippa, Analista en Sistemas de Computación
                  recibido en la Universidad Nacional de Misiones. Actualmente
                  estoy estudiando Ingeniería en Sistemas de Información.
                  Disfruto mucho del desarrollo web, en específico el desarrollo
                  Front-end. Constantemente me encuentro adquiriendo nuevos
                  conocimientos sobre tecnologías y diseño para brindar la mejor
                  experiencia de usuario posible a la hora de desarrollar.
                </SubTitle>
              </TextWrapper>
            </AboutColumn>
          </AboutRow>
        </Container>
      </AboutSection>
    </>
  );
}

export default About;
